<template>
  <div class="staticReports">
    <el-container>
      <el-header>
        <div class="staticReports minWidth loadingtext"
            v-loading="imgLoading"
            element-loading-text="加载中..."
            element-loading-background="rgba(255, 255, 255, 0.8)">
          <el-row class="staticReports-header">
            <el-col :span="17" class="peoplePublichTitle"><span class="mz" @click="$router.back(-1)">流程报表</span><i :class="iconTwo"></i>
            </el-col>
            <el-col :span="6" style="text-align: right">
              <el-button @click="search" type="primary"   round size="mini">搜索</el-button>
              <el-button @click="reset" type="primary"   round size="mini">重置</el-button>
            </el-col>
          </el-row>


          <div class="taticReports-content">
            <!--<div class="resulter">报表条件-->
              <!--<div class="fl fz12 curpoint" @click="showToggle"><span v-text="arrt"></span> <i :class="icon"></i></div>-->
            <!--</div>-->
            <!--<hr>-->
            <div class="statement" v-show="isShow">
              <el-row>
                <el-col :span="24" class="fz16 livingmore" >
                  动态-岗位/人-以“人”统计处理实例最多
                </el-col>
              </el-row>
              <el-row class="staticReports-middle lh35" style="padding-left: 1px !important;">
                <el-col :span="2" :sm="4" :md="1" style="min-width: 94px;"><span>组织选择 : </span></el-col>
                <el-col :span="14" class="peopleles">
                   <span @dblclick="openDailog('project_dialog')">
                     <i class="iconfont icon-organization pl10" @click="openDailog('project_dialog')"></i>
                      <el-input class="input-text" disabled v-model="model.companys" placeholder=""></el-input>
                   </span>
                </el-col>
                <!--<el-col :span="2"><i class="iconfont icon-organization pl10" @click="openDailog('project_dialog')"></i>-->
                <!--</el-col>-->
              </el-row>
              <el-row class="lh35">
                <el-col :span="2" :sm="4" :md="1" style="min-width: 94px; line-height: 30px;"><span class="lh35">时间段 :</span></el-col>
                <el-col  :span="7" :sm="8">
                  <el-radio-group v-model="selectValue" @change="change">
                    <el-radio :label="1">本周</el-radio>
                    <el-radio :label="2">本月</el-radio>
                    <el-radio :label="3">本季度</el-radio>
                    <el-radio :label="4">本年</el-radio>
                    <el-radio :label="5">其他：</el-radio>
                  </el-radio-group>
                </el-col>
                <el-col  :span="7" :sm="6" v-if="selectValue === 5 ">
                  <div class="border">
                    <el-date-picker
                      size="mini"
                      v-model="model.date"
                      type="daterange"
                      value-format="yyyy-MM-dd"
                      range-separator="~"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期">
                    </el-date-picker>
                  </div>
                </el-col>
                <el-col :span="4" class="buttonWrap">

                  <el-button type="primary" @click="exportList" round class="upload"  size="mini"><i class="iconfont icon-export"></i>导出</el-button>
                </el-col>
              </el-row>
            </div>
          </div>

          <div class="taticReports-content">
            <!--<div class="resulter " @click="showToggleTwo">报表结果-->
              <!--<div class="fl"><span v-text="arrtTwo"></span> <i :class="iconTwo"></i></div>-->
            <!--</div>-->

            <!--<hr>-->
         <div class="statement">
                <el-table
                  ref="singleTable"
                  :data="tableData"
                  highlight-current-row
                  :height="isShow?fullHeight-318:fullHeight-215"
                  border
                  @sort-change="sortChange"
                  style="width: 100%">
                  <el-table-column
                    prop="rowNumber"
                    type="index"
                    width="50"
                    sortable='custom'
                    label="排名">
                  </el-table-column>
                  <el-table-column
                    prop="userName"
                    sortable='custom'
                    label="人员">
                  </el-table-column>
                  <el-table-column
                    prop="department"
                    min-width="300"
                    sortable='custom'
                    label="部门">
                  </el-table-column>
                  <el-table-column
                    prop="approvalCnt"
                    label="处理流程实例数" width="200" sortable='custom'>
                  </el-table-column>
                  <div slot="empty" v-show="!imgLoading">
                    <p :style="{'marginTop': '23px'}">暂无数据</p>
                  </div>
                  <div slot="append">
                    <!--分页-->
                    <page :pageIndex="pageIndex" :total="total" :pageSize="pageSize" :pageSizesList="pageSizesList"
                    @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></page>
                  </div>
                </el-table>
              </div>
            
          </div>
          

        </div>
        <component :is="comp.component" v-if="showdialog"
                   @hideComponent="hideComponent" v-on:sureSubmit="showMsgfromchild" v-bind:childDataUrl="childDataUrl">

        </component>

      </el-header>
    </el-container>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex';
  import util from '../js/util';
  import moment from 'moment';
  import Format from '../js/format';
  import projectdialog from '../components/dailog/project_dialog.vue';
  import loading from '../plugins/loading/loading';
  import page from "../plugins/page/index";

  /*本周*/
  var firstDay = moment().day(0).format('YYYY-MM-DD');
  var lastDay = moment().day(6).format('YYYY-MM-DD');
  /*本月*/
  var firstMonth = moment().startOf('month').format('YYYY-MM-DD');
  var lastMonth = moment().endOf('month').format('YYYY-MM-DD');
  /*本季度*/
  var firstSeason = moment().startOf('quarter').format('YYYY-MM-DD');
  var lastSeason = moment().endOf('quarter').format('YYYY-MM-DD');
  /*本年*/
  var firstYear = moment().startOf('year').format('YYYY-MM-DD');
  var lastYear = moment().endOf('year').format('YYYY-MM-DD');

  export default {
    name: '',
    mounted() {
      // this.getList()
    },
    components: {
      'project_dialog': projectdialog,
      'page': page
    },
    data() {
      return {

        oneCheck:true,
        OrgId: '',
        fullHeight: document.documentElement.clientHeight,
        selectValue: 1,
        tableData: [],
        type: 1,
        isShow: true,
        isShowtwo: true,
        arrtTwo: '收起',
        iconTwo: 'el-icon-arrow-down',
        model: {
          selectForm: {
            departmentID:''
          },
          date: [],
          companys:''
        },
        comp: {},
        childDataUrl: {url: ''},//传给子组件的值
        showdialog: false,
        pageIndex: 1,
        total: 0,
        pageSize: 10,
        pageSizesList: [10, 20, 50, 100],
        orderBy:'',
        processID:'',
        start:'',
        end:'',
        startDate:firstDay,
        endDate:lastDay,
        // 加载动画
        imgLoading: false
      }
    },
    methods: {

      change(){
        switch (this.selectValue) {
          case 1:
            this.startDate = firstDay;
            this.endDate = lastDay;
            break;
          case 2:
            this.startDate = firstMonth;
            this.endDate = lastMonth;
            break;
          case 3:
            this.startDate = firstSeason;
            this.endDate = lastSeason;
            break;
          case 4:
            this.startDate = firstYear;
            this.endDate = lastYear;
            break;
          case 5:
            this.startDate = this.model.date[0] ? `${this.model.date[0]} 00:00:00` : '';
            this.endDate = this.model.date[1] ? `${this.model.date[1]} 23:59:59` : '';
            break;
        }

      },

      //表格排序
      sortChange(column){
        this.orderBy =`${column.prop} ${column.order.replace('ending','')}`;
        this.getList();
      },
      showToggle() {
        this.isShow = !this.isShow;
        if (this.isShow) {
          this.arrt = "收起";
          this.icon = "el-icon-arrow-up"
        } else {
          this.arrt = "展开";
          this.icon = "el-icon-arrow-down"
        }
      },
      showToggleTwo() {
        this.isShowtwo = !this.isShowtwo;
        if (this.isShowtwo) {
          this.arrtTwo = "收起";
          this.iconTwo = "el-icon-arrow-up"
        } else {
          this.arrtTwo = "展开";
          this.iconTwo = "el-icon-arrow-down"
        }


      },
      search() {
        this.getList();
      },
      //拉取列表数据
      getList() {
        if (this.model.selectForm.departmentID.length === 0) {
          this.$message.warning('请选择组织进行查询！');
          return;
        }
        // loading.startLoading();
        this.imgLoading = true;
        this.change();
        // 判断是否存在localStorage
        if(localStorage.todoPageSize) {
          this.pageSize = Number(localStorage.todoPageSize)
        }
        let data = {
          "startDate": this.startDate,
          "endDate": this.endDate,
          "deptID": this.model.selectForm.departmentID,
          "pageIndex":this.pageIndex-1,
          "pageSize":this.pageSize,
          "orderBy": this.orderBy,
        };

        this.$axios.post('/api/StatisticalPeopleProInst/page', data).then((result) => {
          this.tableData = result.data.statisticalPeopleProInst;
          this.total = result.data.total;
          // loading.endLoading();
          this.imgLoading = false;
        }).catch((err) => {
          // loading.endLoading();
          this.imgLoading = false;
          this.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        })
      },


      reset() {
        this.childDataUrl.chooseProject = new Array();
        this.$nextTick(() => {
          this.selectValue = 1;
          this.startDate = '';
          this.endDate = '';
          this.model.date = [];
          this.model.companys = '';
          this.model.selectForm.departmentID = '';
        })
      },

      handleSizeChange(val) {
        var pageSize = `${val}`;
        this.pageIndex = 1;
        this.pageSize = parseInt(pageSize);
        localStorage.todoPageSize = val;
        this.getList();
      },
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.getList();
      },

      //导出功能获取数据


      //导出点击触发
      exportList() {
        this.change()
        loading.startLoading({
            lock: true,
            text: '数据量过大，正在拼命加载中...',
            background: 'rgba(255, 255, 255, 0.9)',
            target: document.querySelector('.loadingtext')
          });
         let obj={
           "startDate": this.startDate,
           "endDate": this.endDate,
           "deptID": this.model.selectForm.departmentID,
           "pageIndex":0,
           "pageSize":1000,//this.total,
           "orderBy": this.orderBy

         };
          this.$axios.post('/api/exportExcelForTable', {'tableName':'动态-岗位/人-以“人”统计处理流程实例最多','params':obj}, {responseType: 'blob'}).then((result) => {
            loading.endLoading();
            const content = result.data;
            const dowLoadFileName = '动态-岗位/人-以“人”统计处理流程实例最多';
            util.exportSearchList(dowLoadFileName, content)

            }).catch((err) => {
            loading.endLoading();
            this.$message.error(`导出失败!ERROR MESSAGE:${err}`);
          })
      },

      //-------------------------------      弹窗
      /**
       * 打开弹出框组件
       */
      openDailog(type, url) {
        // 弹出框类型
        this.comp.component = type;
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
        if (type == 'project_dialog') {//组织
          this.childDataUrl.getTree = 'getTree';
          this.childDataUrl.isFirst='2';
        }
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        //选择组织架构
        if (this.oneCheck) {
          let txt = data.last.lastNode;
          this.model.companys_t = txt;
          this.model.companys = txt;
          if(this.model.companys.length > 15){
            this.model.companys = this.model.companys.substr(0,20)+'...';
          }
          this.model.selectForm.departmentID = data.item.orgID;
          this.model.selectForm.companyCode = data.last.lastId.toString();
          this.childDataUrl.chooseProject = data;
        }
        this.hideComponent();
      },
      //弹窗 子组件 调用触发事件
      sureSubmit(data) {
      },
      //-------------------------------      弹窗
    }
  }
</script>
<style lang="scss" scoped>
  @media screen and(min-width:1281px) and (max-width:1550px) {
    .lh35 {
      .el-radio + .el-radio {
        margin-left: 10px !important;
      }
    }
  }
  .peopleles{
    position: relative;
    .icon-organization{
      position: absolute;
      top: 0;
      right: 2%;
      z-index: 1;
    }
  }
  .staticReports .el-header .staticReports .staticReports-header{
    padding: 0px;
    height: 48px;
    line-height: 48px;
  }
  .peoplePublichTitle{
    padding-left: 25px;
    .el-icon-arrow-down{
      color: #0092ff;
      font-weight: 600;
      /*color: #64d5ff;*/
      font-size: 14px;
      font-weight: bold;
    }
  }
  .el-container > .el-header{
    padding: 0px;
    border-bottom:none;
     .el-row{
     /* height: -1px;
      line-height: 2px;*/
     }
  }
  .pl10 {
    padding-left: 10px;
  }

  .pl18 {
    padding-left: 18px;
  }

  .fz16 {
    font-size: 15px;
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  i {
    font-style: normal;
  }

  a {
    color: #42b983;
  }
.livingmore{
  padding-bottom: 15px;
  line-height: 0px;
  margin-top: 15px;
}
  .staticReports {
    font-family: "Microsoft YaHei";

    .el-header {
      padding: 0;
      .staticReports {
        /*padding: 0px 10px 0px 10px;*/
        padding: 0px;
        .taticReports-content {
          background: #ffffff;
          /*margin-top: 10px;*/
          border-radius: 10px;
          padding: 0px 20px 10px 5px;
          color: #464646;
          font-style: normal;
          font-size: 16px;
          .statement table th {
            background-color: #20a2ff;
          }
          hr {
            height: 1px;
            background-color: #ccc;
            border: none;
            margin-bottom: 20px;
          }
          .pagination {
            margin-top: 20px;
            .pagesize {
              a {
                display: inline-block;
                height: 25px;
                width: 25px;
                border: 1px solid #ccc;
                line-height: 25px;
                text-align: center;
                /*background: #4aa1f1;*/
                text-decoration: none;
                color: #606266

              }
              .first {
                background-color: #4da6f6;
                color: #ffffff;

              }
            }
          }
          .resulter {
            border-left: 3px solid #00a1f1;
            height: 18px;
            line-height: 18px;
            padding-left: 15px;
            font-size: 15px;
            margin-bottom: 15px;

            .fl {
              float: right;
            }

          }
        }
        .staticReports-middle {
          line-height: 30px;
          font-size: 14px !important;

          .staticReports-middle span {
            color: #cccccc;
          }
          .el-icon-rank {
            color: #22a5fd;
            font-size: 25px;
            line-height: 40px;
            margin-left: 10px;
          }
        }
        text-align: left;
        .staticReports-header, .staticReports-middle {
          /*background: #ffffff;*/
          /*border-radius: 10px;*/
          .mz {
            display: inline-block;
            font-size: 16px;
            /*padding: 20px 0 20px 20px;*/
            color: #000000;
            cursor: pointer;
            margin-right: 10px;
          }

          h3 {
            display: inline-block;
            font-size: 16px;
            padding-left: 10px;
          }
          h3:nth-child(1) {
            color: #22a5fd;
            cursor: pointer;
          }
        }
      }
    }
    .table-popover{
       margin: 0 auto;
    }
  }


</style>




